.login__form,
.registr__form {
  padding: 32px 20px;

  margin-left: auto;
}

.form__border {
  display: block;
  text-align: center;
  max-width: 280px;
  margin-left: auto;
  margin-right: auto;
}

.google__auth {
  display: inline-block;
  text-decoration: none;
  text-align: center;
  min-width: 150px;
  padding: 11px 14px;
  margin-bottom: 28px;
  font-size: 16px;
  font-weight: 700;
  color: #707375;
  background-color: #f5f7fa;
  cursor: pointer;
  border: 1px solid #f5f7fa;
  box-shadow: 0px 2px 2px rgba(9, 30, 63, 0.15);
}

.google__container {
  display: flex;
}

.svg__google {
  width: 18px;
  height: 18px;
  margin-right: 16px;
}

.google__text {
  padding: 0;
  margin: 0;
  font-family: 'Roboto';
  font-style: normal;
}

.label__title {
  text-align: left;
  font-size: 14px;
  color: white;
  margin: 0;
  margin-bottom: 8px;
}

.input {
  padding: 12px 8px;
  min-width: 280px;
  margin-bottom: 10px;
  background: #f5f7fa;
  box-shadow: inset 0px 1px 2px rgba(29, 29, 27, 0.15);
  outline: none;
  border: none;
}

.input::placeholder {
  font-weight: 400;
  font-size: 14px;
  color: #a6abb9;
}

.form__button {
  display: block;
  padding: 20px 0;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
  min-width: 280px;
  font-weight: 600;
  color: #ffffff;
  background-color: #ff6b08;
  border: 1px solid #ff6b08;
  cursor: pointer;
}

.form__button:hover {
  background-color: #d15807;
}

.error,
.warning,
.label__star {
  margin: 0;
  margin-left: 5px;
  font-size: 12px;
  text-transform: lowercase;
  color: #f25137;
}

.warning {
  text-align: left;
  margin-bottom: 10px;
}

.default__count {
  display: block;
  height: 14px;
  margin-bottom: 10px;
}

.auth__describe,
.auth__verify {
  margin-top: 10px;
  font-size: 13px;
  color: #898f9f;
}

.auth__verify {
  text-align: left;
}

.auth__link,
.authforgot__link,
.loginauth__link {
  display: inline-block;
  text-align: center;
  font-size: 13px;
  color: #ff6b08;
  text-decoration: underline;
}

.loginauth__link {
  margin-left: 5px;
}

.button__verify {
  display: inline-block;
  color: #ff6b08;
  background: none;
  border: none;
  padding: none;
  margin: none;
  cursor: pointer;
  text-decoration: underline;
}

/* =============================================== */

.quote {
  font-size: 13px;
  color: #242a37;
  margin: 0;
  margin-bottom: 16px;
}

.hr {
  width: 100px;
  color: rgba(36, 42, 55, 0.5);
  margin-bottom: 12px;
}

.author {
  margin: 0;
  font-size: 14px;
  color: #898f9f;
}

/* =============================================== */
.registration__text {
  padding: 32px 25px 40px;
}

.title {
  margin: 0;
  margin-bottom: 32px;
  font-weight: 400;
  font-size: 34px;
  color: #242a37;
}

.subtitle {
  font-weight: 500;
  font-size: 20px;
  color: #fff;
  margin: 0;
  margin-bottom: 14px;
}

.registration__list {
  margin-bottom: 24px;
}

.registration__list2 {
  margin-bottom: 60px;
}

.registration__item {
  display: inline-flex;
  align-items: center;
  margin-bottom: 12px;
  list-style: none;
}

.registration__item::before {
  content: '';
  width: 4px;
  height: 8px;
  margin-right: 12px;
  background-size: contain;
  /* background-image: url("../../../img/arrow.svg"); */
}

.item__text {
  font-size: 14px;
  font-weight: 500;
  color: #898f9f;
}

/* ! ==================================== !!! MEDIA !!! =============================== ! */

@media screen and (min-width: 769px) {
  .login__form,
  .registr__form {
    padding: 64px 185px;
  }

  .form__border {
    max-width: 400px;
    padding: 40px;
    background-color: #ffffff;
  }

  .label__title {
    color: #898f9f;
  }

  .input {
    padding: 12px 12px;
    min-width: 320px;
  }

  .form__button {
    min-width: 320px;
  }

  .warning,
  .default__count {
    margin-bottom: 22px;
  }

  .subtitle {
    color: #242a37;
  }

  /* ===============================================  */
  .quote {
    font-size: 24px;
    margin-bottom: 20px;
  }

  .hr {
    width: 150px;
  }

  .author {
    font-size: 24px;
  }

  /* ===============================================  */
  .registration__text {
    padding: 64px 160px 88px;
  }

  .title {
    margin-bottom: 48px;
  }

  .registration__list {
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 1281px) {
  .section {
    display: flex;
  }

  .form__button {
    margin-bottom: 32px;
  }

  .login__form {
    padding: 185px 75px 175px;
  }

  .registr__form {
    padding: 90px 75px;
  }

  .registration__text {
    padding: 180px 128px 0px;
  }
}

.label_password {
  position: relative;
}

.svg__eyeOffCont {
  position: absolute;
  top: 50%;
  right: 15px;
  /* stroke: #898F9F; */
}

.svg__eyeOffCont:hover,
.svg__eyeOffCont:focus {
  cursor: pointer;
  stroke: #ff6b08;
}

.svg__eyeOffContActive {
  stroke: #ff6b08;
}

.svg__eyeOff {
  width: 20px;
  height: 20px;
  stroke: inherit;
}
